import userModel from "@/model/user"
import Provision from '@/components/Provision'
import store from '@/store/store'
import * as types from '@/store/types'
  export default {
      name: "Login",
      data()
      {
        return {
          email:"",
          code:"",
          code_tip:"获取验证码",
          time:60,
          remaining:60,
          inTime:true,//是否到点
          token:"",
        }
      },
      components:{
        Provision,

      },
      methods:
      {
        loginout:function()
        {
          store.commit(types.LOGOUT);
        },
        //返回
        goback:function()
        {
          this.$router.back(-1)
        },
        //倒计时
        countDown:function()
        {
          var that=this
          this.inTime=false
          var coutDownTimeStamp=setInterval(function()
          {
            that.code_tip="剩余"+that.remaining+"秒"
            if(that.remaining===0)
            {
              clearInterval(coutDownTimeStamp)
              that.code_tip="获取验证码"
              that.remaining=that.time+1
              that.inTime=true
            }
            that.remaining--
          },1000)
        },
        //获取验证码
        getCode:function()
        {
          if(this.inTime)
          {

            if(!this.email)
            {
              this.$vux.toast.show({
                text: '请输入邮箱',
                type:'text',
                position:"bottom"
              });
              return;
            }
            if(!(/^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/.test(this.email)))
            {
              this.$vux.toast.show({
                text: '邮箱格式错误',
                type:'text',
                position:"bottom"
              });
              return;
            }
            var that=this;
            userModel.getCode(this.email,function(ret)
            {
              if(ret&&ret.isSuccess)
              {
                //倒数开始
                that.countDown();
              }
              if(ret&&ret.message)
                that.$vux.toast.show({
                  text: ret.message,
                  type:'text',
                  position:"bottom"
                });

            });
          }
        },
        //登陆
         login:function() {
           console.log(store.state)
           if (!this.email) {
             this.$vux.toast.show({
               text: '请输入邮箱',
               type: 'text',
               position: "bottom"
             });
             return false;
           }
           if (this.email && !(/^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/.test(this.email))) {
             this.$vux.toast.show({
               text: '请输入正确的邮箱',
               type: 'text',
               position: "bottom"
             });
             return false;
           }
           if (!this.code) {
             this.$vux.toast.show({
               text: '请输入验证码',
               type: 'text',
               position: "bottom"
             });
             return false;
           }
           if (!/\d{6}/.test(this.code)) {
             this.$vux.toast.show({
               text: '请输入6位数的验证码',
               type: 'text',
               position: "bottom"
             });
             return false;
           }
           var that = this


           that.$vux.loading.show({
             text: '登录中'
           })
           //登录
           userModel.login(that.email, that.code, function (ret, err) {
             that.$vux.loading.hide()
             if (ret.data && ret.data["token"]) {
               //保存token
               store.commit(types.TOKEN, ret.data["token"]);

               let redirect = decodeURIComponent(that.$route.query.redirect || '/main');

               that.$router.push({
                 path: redirect
               })
             }
             if(ret&&ret.message)
             {
               that.$vux.toast.show({
                 text: ret.message,
                 type:'text',
                 position:"bottom"
               });
             }
           })
         }


      },
      created()
      {

      }
  }



